import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import cookiesData from './cookiesData'; // Import the cookie data

// Styled components
const PageContainer = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
`;

const HeaderLeft = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 5%;
`;

const Icon = styled.img`
  margin-right: 10px;
`;

const Title = styled.h1`
  font-size: 18px;
  margin: 0;
`;

const SubTitle = styled.p`
  margin: 0;
  font-size: 14px;
  color: #666;
`;

const FilterContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 0px;
  margin-bottom: 20px;
`;

const FilterTitle = styled.p`
  font-size: 14px;
`;

const FilterRow = styled.div`
  display: flex;
  gap: 10px;
  align-items: center;
`;

const FilterOption = styled.img`
  cursor: pointer;
  border: ${({ selected }) => (selected ? '2.5px solid #000' : '2px solid transparent')};
  border-radius: 10px;
  justify-content: center;
`;

const CookiesGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 20px;
  width: 100%;
  max-width: 1200px;
  justify-items: center;
`;

const CookieCard = styled.div`
  width: 90px; /* Fixed width */
  height: 130px; /* Fixed height */
  text-align: center;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const getBackgroundColor = (grade) => {
  switch (grade) {
    case 'common':
      return {
        background: 'linear-gradient(to bottom, #602A03 0%, #E4B593 100.00%)'
      };
    case 'rare':
      return {
        background: 'linear-gradient(to bottom, #3D548A 0%, #869ED4 100.00%)'
      };
    case 'epic':
      return {
        background: 'linear-gradient(to bottom, #6F0EBA 0%, #C36AE8 100.00%)'
      };
    default:
      return {
        background: 'white'
      };
  }
};

const CookieBackground = styled.div`
  ${(props) => getBackgroundColor(props.grade)};
  justify-content: center;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%; /* Match width with CookieCard */
  height: 100%; /* Adjust height to fit the card */
  padding: 5px 5px 0px 5px;
`;

const CookieImage = styled.img`
  width: 80px; /* Fixed width */
  height: 80px; /* Fixed height */
  border-radius: 10px;
`;

const CookieName = styled.p`
  background-color: #353535;
  color: white;
  margin: 0px;
  padding: 5px;
  border-radius: 0 0 10px 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%; /* Match width with CookieCard */
`;

const IconContainer = styled.div`
  display: flex;
  justify-content: center;
  gap: 50px;
  margin-top: auto;
`;

// Filter options
const gradeOptions = ['epic', 'rare', 'common'];
const attributeOptions = ['water', 'fire', 'light', 'grass', 'ground', 'dark'];
const typeOptions = ['shooting', 'assist', 'cut', 'charge'];
const markOptions = ['mark', 'none'];

const CookiesInfo = () => {
  const [selectedGrades, setSelectedGrades] = useState([]);
  const [selectedAttributes, setSelectedAttributes] = useState([]);
  const [selectedTypes, setSelectedTypes] = useState([]);
  const [selectedMarks, setSelectedMarks] = useState([]);
  const navigate = useNavigate();

  const handleFilterClick = (filterType, value) => {
    const updateFilter = (filter, setFilter) => {
      if (filter.includes(value)) {
        setFilter(filter.filter((item) => item !== value));
      } else {
        setFilter([...filter, value]);
      }
    };

    if (filterType === 'grade') {
      updateFilter(selectedGrades, setSelectedGrades);
    } else if (filterType === 'attribute') {
      updateFilter(selectedAttributes, setSelectedAttributes);
    } else if (filterType === 'type') {
      updateFilter(selectedTypes, setSelectedTypes);
    } else if (filterType === 'mark') {
      updateFilter(selectedMarks, setSelectedMarks);
    }
  };

  const handleCookieClick = (cookiePath) => {
    navigate(`/cookies/${cookiePath}`);
  };

  const filteredCookies = cookiesData.filter((cookie) => {
    return (
      (selectedGrades.length === 0 || selectedGrades.includes(cookie.grade)) &&
      (selectedAttributes.length === 0 || selectedAttributes.includes(cookie.attribute)) &&
      (selectedTypes.length === 0 || selectedTypes.includes(cookie.type)) &&
      (selectedMarks.length === 0 || selectedMarks.includes(cookie.mark))
    );
  });

  return (
    <PageContainer>
      <HeaderLeft>
          <Icon src="https://sseong-mi.github.io/Tower-of-Adventures/images/items/cocodrop.png" alt="Icon" />
        <div>
          <Title>쿠키 정보</Title>
          <SubTitle>세부 정보를 알고싶다면 쿠키를 클릭하세요!</SubTitle>
        </div>
      </HeaderLeft>
      <FilterContainer>
        <FilterRow>
        <FilterTitle>타입</FilterTitle>
          {gradeOptions.map((grade) => (
            <FilterOption
              key={grade}
              src={`https://sseong-mi.github.io/Tower-of-Adventures/images/grades/${grade}.png`}
              alt={grade}
              selected={selectedGrades.includes(grade)}
              onClick={() => handleFilterClick('grade', grade)}
            />
          ))}
        </FilterRow>
        <FilterRow>
        <FilterTitle>속성</FilterTitle>
          {attributeOptions.map((attribute) => (
            <FilterOption
              key={attribute}
              src={`https://sseong-mi.github.io/Tower-of-Adventures/images/attributes/${attribute}.png`}
              alt={attribute}
              selected={selectedAttributes.includes(attribute)}
              onClick={() => handleFilterClick('attribute', attribute)}
            />
          ))}
        </FilterRow>
        <FilterRow>
        <FilterTitle>공격</FilterTitle>
          {typeOptions.map((type) => (
            <FilterOption
              key={type}
              src={`https://sseong-mi.github.io/Tower-of-Adventures/images/types/${type}.png`}
              alt={type}
              selected={selectedTypes.includes(type)}
              onClick={() => handleFilterClick('type', type)}
            />
          ))}
        </FilterRow>
        <FilterRow>
        <FilterTitle>표식</FilterTitle>
          {markOptions.map((mark) => (
            <FilterOption
              key={mark}
              src={`https://sseong-mi.github.io/Tower-of-Adventures/images/marks/${mark}.png`}
              alt={mark}
              selected={selectedMarks.includes(mark)}
              onClick={() => handleFilterClick('mark', mark)}
            />
          ))}
        </FilterRow>
      </FilterContainer>
      <CookiesGrid>
        {filteredCookies.map((cookie) => (
          <CookieCard key={cookie.name} onClick={() => handleCookieClick(cookie.path)}>
            <CookieBackground grade={cookie.grade}>
              <CookieImage src={cookie.image} alt={cookie.name} />  
              <IconContainer>
                <img src={`https://sseong-mi.github.io/Tower-of-Adventures/images/attributes/${cookie.attribute}.png`} alt={cookie.attribute} width="20" />
                <img src={`https://sseong-mi.github.io/Tower-of-Adventures/images/types/${cookie.type}.png`} alt={cookie.type} width="20" />
              </IconContainer>
              <CookieName>{cookie.name}</CookieName>
            </CookieBackground>
          </CookieCard>
        ))}
      </CookiesGrid>
    </PageContainer>
  );
};

export default CookiesInfo;
